@import '../../global-styles/variables.scss';

.mlHead{
   background-color: $PrimaryColor;
}
.mlHead-container {
  margin: 0 auto;
  display: flex;
  width: 100%;
  max-width: 1100px;
  padding: 8px;
}
.mlHead-search {
  width: 100%;
  display: flex;
  align-items: center;
}
.mlHead-input {
  width: inherit;
  height: 30px;
  border: 0px;
  font-size: 18px;
}

.mlHead-button {
  width: 45px;
  height: 32px;
  border: 0px;
}