

.ProductCard{
  display: flex;
  padding: 16px;
  justify-content: space-between;
  border-bottom: 1px solid #eeeeee;
  
  &-img {
    width: 180px;
    height: 190px;
    border-radius: 4px;
 }
 &-mainContent{
  padding: 0 16px;
  width: 100%;
 }
 &-price {
  display: flex;
  font-size: 24px;
  margin-bottom: 32px;
  margin-top: 16px;
  align-content: center;
}
&-price img {
  margin-left: 10px;
  height: 21px;
  /* display: inline-block; */
  margin-top: 6px;
}
&-description{
  color: #333333;
  font-size: 18px;
}
&-locationContent{
  font-size: 12px;
  color: #999;
  width: 200px;
}
}